.slider {

        &__wrapper {
            display: flex;
            gap: 10px;
            @media (max-width: 520px) {
            gap: 5px;
            } 
            }

        &__button{
            display: flex;
            align-items: center;
            // flex: 0 0 fit-content;
            @media (max-width: 1170px) {
            flex: 0 0 60px;
            }
            @media (max-width: 992px) {
            flex: 0 0 45px;
            }
            @media (max-width: 768px) {
            flex: 0 0 35px;
            } 
            @media (max-width: 520px) {
            flex: 0 0 25px;
            } 
        }

        &__prev,
        &__next {
            width: 79px;
            height: 79px;
            // background: url("../../img/slider/arrow.svg")  center / full no-repeat $grey;
            border: 3px solid $white;
            border-radius: 50%;
            padding: 15px 0;
            @media (max-width: 1170px) {
            width: 60px;
            height: 60px;
            padding: 10px 0;
            }
            @media (max-width: 992px) {
            // background: url("../../img/slider/arrow.svg")  center / 30% no-repeat $grey;
            width: 45px;
            height: 45px;
            padding: 7px 0;
            }
            @media (max-width: 768px) {
            width: 35px;
            height: 35px;
            padding: 3px 0;
            } 
            @media (max-width: 520px) {
            width: 25px;
            height: 25px;
            } 

            img {
                // width: 30%;
                height: 100%;
            }
        }
        &__next {
            rotate: 180deg;
		}
        &__content{
            flex: 1 1 auto; 
            
        }
		&__sledes {
            width: 100%;
            position: relative;
            display: flex;
            perspective: 300px;
            &::before{
                content: '';
                display: block;
                position: relative;
                padding-top: 76%;
                width: 0;
            }
		}

		&__slide {
            position: absolute;
            top: 0;
            // left: 12%;
            width: 79%;
            height: 100%;
                  &.-active{
                        left: 10%;
                        transform: translate3d(0px, 0, 0px);
                        transition: all 0.3s;
                        z-index: 1000000;
                        
                  }

                  &.-prev{
                        left: -4%;
                        transform: scale(0.9);
                        transition: all 0.2s;
                        z-index: 2;
                  }

                  &.-next{
                       left: 20%; 
                       z-index: 3;
                       transform: scale(0.9);
                       transition: all 0.3s;
                  }

                  &.-other{
                        left: 29%;
                        // transform: translate3d(50px, 0, -50px);
                        transform: scale(0.8);
                        transition: all 0.2s;
                        
                  }
                  &:not(.-active) .slider__img::after,
                  &:not(.-active) .slider__img::before {
                        content: "";
                        display: block;
                        position: absolute;
                        top: 0;
                        left: 0;
                        height: 100%;
                        width: 100%;
                        background-color: rgb(0, 0, 0, 0.2);
                  }
		}

		&__image {
            width: 100%;
            height: 100%;
            position: relative;
		}

		&__img {
            position: absolute;
            // padding-top: 96%;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0%;
            // transform: translateX(-50%);
            border-radius: 20px; 
            overflow: hidden;
            box-shadow: 0 4px 4px 0px rgba(0, 0, 0, 0.25);
                img {
                    position: absolute;
                    top: 0;
                    left: 0;
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                }
		}

		
}