.intagration {
        margin-bottom: calc(174 / $mw * 100%);
        @media (max-width: 768px) {
                margin-bottom: 50px;
            }
        &.-container{
            padding: 0 7.2% 0 4.8%;
        }
		&__title {
            margin-bottom: 74px;
            @media (max-width: 1170px) {
                margin-bottom: 60px;
            }
            @media (max-width: 768px) {
                margin-bottom: 50px;
            }
            @media (max-width: 520px) {
                margin-bottom: 40px;
            }
		}

		&__content {
            display: flex;
            align-items: center;
            @media (max-width: 768px) {
                flex-direction: column;
                
            }
		}

		&__text {
            flex: 0 0 43%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            font-size: calc(12px + 14 * ((100vw - 320px) / ($mw - 320)));
            line-height: 142%;
            padding: 0 20px 0 90px;
            @media (max-width: 1170px) {
                padding: 0 15px 0 65px;
            }
            @media (max-width: 992px) {
                padding: 0 10px 0 40px;
                line-height: 110%;
            }
            @media (max-width: 768px) {
                margin: 0 0 40px 0;
                padding: 0 0px 0 25px;
            }
            @media (max-width: 520px) {
                font-size: calc(12px + 6 * ((100vw - 320px) / (520 - 320)));
                padding: 0 0px 0 25px;
            }
            & *:not(:last-child){
                margin-bottom: 40px;
            @media (max-width: 1170px) {
                margin-bottom: 35px;
            }
                @media (max-width: 992px) {
                margin-bottom: 30px;
            }
            @media (max-width: 768px) {
                margin-bottom: 20px;
            }
                @media (max-width: 768px) {
                margin-bottom: 10px;
            }
            }
		}

		&__bold {
            font-family: "ActayWide", serif;
		}

		&__regular {
            
		}

		&__slider {
            flex: 0 0 57%;
            @media (max-width: 768px) {
               width: 100%;
            }
		}
}