.opportunius {
        margin-bottom: calc(314 / $mw * 100%);
        @media (max-width: 768px) {
                margin-bottom: 67px;
            }
		&__list {
            padding: 0;
		}

		&__point:not(:last-of-type) {
            margin-bottom: 28px;
            
            @media (max-width: 1170px) {
			    margin-bottom: 25px;

			}
            @media (max-width: 992px) {
			    margin-bottom: 20px;

			}
            @media (max-width: 768px) {
			    margin-bottom: 10px;

			}
            @media (max-width: 520px) {
			    margin-bottom: 7px;

			}
		}

        &__title{
            margin-bottom: 75px;
            
            @media (max-width: 1170px) {
			    margin-bottom: 65px;

			}
            @media (max-width: 992px) {
			    margin-bottom: 55px;

			}
            @media (max-width: 768px) {
			    margin-bottom: 45px;

			}
            @media (max-width: 520px) {
			    margin-bottom: 35px;

			}
        }
}
