.about {
        margin-bottom: calc(260 / $mw * 100%);
        @media (max-width: 768px) {
                margin-bottom: 76px;
            }
		&__title {
            margin-bottom: 14px;
            
		}

		&__heading {
            width: calc(1020 / $mw * 100%);
            margin: 0 auto;
            z-index: 5;
            position: relative;
            img{
                width: 100%;
            }
            @media (max-width: 520px) {
                width: 100%;
            }
		}

		&__content {
            display: flex;
            column-gap: 4rem;
            align-items: center;
            margin-top: calc(185 / $mw * (-100%));
            position: relative;
            
            @media (max-width: 520px) {
                
                display: block;
                margin-top: calc(32 / 320 * (-100%));
            }
            
		}

		&__image {
            flex: 0 0 52%;
            position: relative;
            border-radius: 40px;
            overflow: hidden;
            @media (max-width: 720px) {
                border-radius: 20px;
            }
            @media (max-width: 520px) {
               border-radius: 5px;
               padding-top: 152%;
               &::after, &::before{
                content: '';
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                display: block;
                background: linear-gradient(180deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 40%);
                opacity: 0.2;
                z-index: 2;
               }
               }
            img {
                max-width: 100%;
                
               @media (max-width: 520px) {
               position: absolute;
               top: 0;
               left: 0;
               object-fit: cover;
               width: 100%;
               height: 100%;
               object-position: -50px;
               
               
               }
            }
            
		}

		&__text {
            font-family: "ActayWide", serif;
            // font-size: 30px;
            font-size: calc(10px + 20 * ((100vw - 320px) / ($mw - 320)));
            line-height: calc(46 / 30 * 100%);
            flex: 0 1 608px;
            z-index: 6;
            position: relative;
            @media (max-width: 768px) {
                font-size: calc(10px + 5 * ((100vw - 320px) / (720 - 320)));
                padding-top: 30px;
            }
            @media (max-width: 520px) {
                font-size: calc(10px + 5 * ((100vw - 320px) / (520 - 320)));
                position: absolute;
                width: 75%;

                top: 0;
                left: 50%;
                transform: translateX(-50%);
                padding-top: 33px;
                color: #fff;
                text-align: center;
            }
            //padding-top: calc(185 / 1680 * (100%));
		}
}
