.point {
        //width: fit-content;
		&__wrapper {
            width: fit-content;
            display: flex;
            gap: 31px;
            padding: 0 73px 0 0;
            align-items: center;
            border-radius: 40px;
            @media (max-width: 1170px) {
            padding: 0 55px 0 0;
            gap: 25px;
            }
            @media (max-width: 992px) {
            padding: 0 45px 0 0;
            gap: 20px;
            }
            @media (max-width: 768px) {
            padding: 4px 30px 4px 10px;
            gap: 15px;
            } 
            @media (max-width: 520px) {
            padding: 4px 18px 4px 5px;
            gap: 9px;
            } 
            
		}

		&__number {
            flex: 0 0 55px;
            //width: fit-content;
            border: 3px solid $white    ;
            width: 55px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            box-shadow: 0 4px 4px 0px rgba(0, 0, 0, 0.25);
            font-family: 'ActayWide', serif;
            font-size: calc(15px + 10 * ((100vw - 320px) / ($mw - 320)));
            line-height: calc(58 / 35 * 100%);
            
            @media (max-width: 1170px) {
            flex: 0 0 50px;
            width: 50px;
            }
            @media (max-width: 992px) {
            flex: 0 0 45px;
            width: 45px;
            }
            @media (max-width: 768px) {
            flex: 0 0 40px;
            width: 40px;
            } 
            @media (max-width: 520px) {
            flex: 0 0 32px;
            width: 32px;
            }
            &::before{
                content: '';
                padding-top: 100%;
                display: block;
                width: 0;
               }
		}

        &__circle{
            transform: translateY(5px);
            @media (max-width: 992px) {
			    transform: translateY(2px);
		    }
        }

		&__content {
            flex: 1 1 auto;
		}

		&__text {
            padding: 8px 0 5px;
            line-height: calc(31 / 30 * 100%);
            font-size: calc(11px + 9 * ((100vw - 320px) / ($mw - 320)));
            color: $black;
            width: 100%;
            white-space: wrap;
            span {
                white-space: nowrap;
                font-family: "ActayWide", serif;
            }
            @media (max-width: 768px) {
                   line-height: 118%;
                   padding: 0;
                }
            br {
                @media (min-width: 380px) {
                   display: none;
                }
            }
		}
}
