$mw: 1780;

*,
*::before,
*::after {
	-moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	 justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}



.-container {
    
			//max-width: 1680px;
			// max-width: 1780px;
			max-width: $mw + px;
			width: 100%;
			padding: 0 7.2% 0 4.8%;
			margin: 0 auto;
	
			// @media (max-width: 1170px) {
			// 	max-width: 950px;
			// 	padding: 0;
			// }
	
			// @media (max-width: 992px) {
			// 	max-width: 750px;
			// 	padding: 0;
			// }
	
			@media (max-width: 768px) {
				padding: 0 21px;
			}

}

body{
	background: #E6E6E6;
}
