.footer {
    background: url(../../img/footer.svg) / cover no-repeat  $red;
    padding: 30px 0 20px 0;
    @media (max-width: 768px) {
            padding: 20px 0 47px 0;
            }
        @media (max-width: 768px) {
                margin: 0 0 15px 0;
                box-shadow: 0 4px 4px 0 rgb(0, 0, 0, 0.25);
            }
		&__body {
            display: flex;
            margin: 0 0 35px 0;
            @media (max-width: 768px) {
            margin: 0 0 45px 0;
            flex-direction: column;
            
            }
		}

		&__heading {
            flex: 0 0 50%;
            @media (max-width: 992px) {
               flex: 0 0 60%; 
            }
		}

		&__title {
            margin: 0 0 55px 0;
		}

		&__text {
            text-transform: uppercase;
            font-size: calc(24px + 10 * ((100vw - 320px) / (1781 - 320)));
            line-height: 120%;
            color: $white;
            padding: 0 0 0 70px;
            font-family: "ActayWide", serif;
            @media (max-width: 768px) {
            padding: 0;
            text-align: center;
            margin: 0 0 50px 0;
                  }
		}

		&__contacts {
                  @media (max-width: 768px) {
                  text-align: center;
            }
		}

		&__feedback {
            display: block;
            background-color: $red;
            font-size: calc(17px + 10 * ((100vw - 320px) / ($mw - 320)));
            line-height: 115%;
            padding: 15px 40px 13px;
            white-space: nowrap;
            border: 3px solid $white;
            border-radius: 40px;
            color: $white;
            font-family: "ActayWide", serif;
            margin-bottom: 90px;
            width: fit-content;
            @media (max-width: 1170px) {
            padding: 20px 45px 15px;
            margin-bottom: 70px;
            }
            @media (max-width: 992px) {
            padding: 17px 35px 13px;
            margin-bottom: 50px;
            }
            @media (max-width: 768px) {
            align-self: center;   
            padding: 11px 30px;
            border: 1px solid $white;
            margin: 0 auto 30px;
            } 
            @media (max-width: 520px) {
            padding: 9px 25px;
            margin-bottom: 15px;
            
            } 

		}

		&__link {
		}

		&__email {
                  font-size: calc(10px + 15 * ((100vw - 320px) / ($mw - 320)));
                  line-height: 121%;
                  color: $white;
                  margin-bottom: 14px;
                  padding-left: 50px;
                  @media (max-width: 768px) {
                  padding: 0;
                  }
		}

		&__telegram  {
                  font-size: calc(10px + 15 * ((100vw - 320px) / ($mw - 320)));
                  line-height: 121%;
                  color: $white;
                  display: flex;
                  gap: 15px;
                  padding-left: 50px;
                  align-items: center;
                  @media (max-width: 768px) {
                  padding: 0;
                  justify-content: center;
                  gap: 7px;
                  }
                  &:hover .footer__social{
                        text-decoration: underline solid $white;
                  }
		}

		&__icons {
                  @media (max-width: 768px) {
                  padding: 0;
                  justify-content: center;
                  img {
                        width: 25px;
                  }
                  }
                  @media (max-width: 520px) {
                  padding: 0;
                  justify-content: center;
                  img {
                        width: 20px;
                  }
                  }
		}

		&__social {
		}

		

		&__link {
		}

		&__caption {
            line-height: 110%;
            font-size: calc(10px + 14 * ((100vw - 320px) / ($mw - 320)));
            text-transform: uppercase;
            color: $white;
            padding: 0 0 0 70px;
            @media (max-width: 768px) {
                text-align: center;
                padding: 0;
            }
		}
}
