.title {
      font-size: 0;      
		&__link {
            border-radius: 40px 0 0 40px; 
            display: block;
            overflow-x: hidden; 
            overflow-y: visible;
            position: relative;
            &:hover {
               & .title__text.title__text_unvisible{
                @media (min-width: 768.1px) {
                transform: translateX(0); 
                visibility: visible;
                opacity: 1;
                }
               
               }
            }
            
            &_visible{
                overflow: visible;
            }
            
		}

        &__image{
            flex: 0 0 55px;
            //width: fit-content;
            border: 3px solid $white    ;
            width: 55px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            box-shadow: 0 4px 4px 0px rgba(0, 0, 0, 0.25);
            z-index: 2;
            position: relative;
            left: -1px;
            @media (max-width: 1170px) {
            flex: 0 0 50px;
            width: 50px;
            }
            @media (max-width: 992px) {
            flex: 0 0 45px;
            width: 45px;
            }
            @media (max-width: 768px) {
            flex: 0 0 40px;
            width: 40px;
            } 
            @media (max-width: 520px) {
            flex: 0 0 32px;
            width: 32px;
            }
            
            &::before{
                content: '';
                padding-top: 100%;
                display: block;
                width: 0;
            }

            img {
                width: 50%;
            //    @media (max-width: 1170px) {
                
            //     width: 40px;  
            //     }
            //     @media (max-width: 992px) {
                
            //     width: 35px;  
            //     }
            //     @media (max-width: 768px) {
                
            //     width: 30px; 
            //     } 
            //     @media (max-width: 520px) {
                
            //     width: 20px;  
            //     }
            }
        }

		&__text {
            background: #fff;
            box-shadow: 0 4px 4px 0px rgba(0, 0, 0, 0.25);
            border-radius: 40px;
            position: absolute;
            top: 0;
            left: 0;
            text-transform: uppercase;
            color: $black;
            max-height: 99% !important; 
            height: 99%  !important;
            font-family: 'ActayWide', serif;
            font-size: calc(15px + 10 * ((100vw - 320px) / ($mw - 320)));
            line-height: calc(58 / 35 * 100%);
            transition: all 0.3s linear; 
            //padding: 13px calc(55 / 1680 * 100%) 8px calc(103 / 1680 * 100%);
            padding: 13px 40px 8px 80px;
            transform: translateX(-100%); 
            display: flex;
            align-items: center;

            &.title__text_unvisible{
                visibility: hidden;
                opacity: 0;
            }

            span{
                white-space: nowrap;
            }
            
            @media (max-width: 1170px) {
              padding: 13px 30px 8px 65px;  
            }

            @media (max-width: 520px) {
                font-size: 15px;
                padding: 5px calc(20 / 286 * 100%) 3px calc(43 / 286 * 100%);
            }
            &_visible{
               transform: translateX(0);  
            }
            
            
		}
}

