.general {
        padding-top: 43px;
        @media (max-width: 768px) {
                margin-bottom: 55px;
        }
        & .-container{
            padding: 0 4.3% 0 2.4%;

            @media (max-width: 768px) {
                padding: 0;
            }
        }

		&__grid {
            display: grid;
            // grid-template-columns: minmax(calc(643 / 1680 * 100%), 50%) 1fr;
            grid-template-columns: 33% 1fr;
            // grid-template-rows: auto auto minmax(75px, 1fr);
            grid-template-rows: auto auto ;
            @media (max-width: 1170px) {
                grid-template-columns: 40% 1fr;
            }
            @media (max-width: 992px) {
            grid-template-rows: auto auto;
            // grid-template-rows: auto auto minmax(180px, 1fr);
            }
            @media (max-width: 768px) {
                grid-template-columns: 1fr;
            }
            &:has(.roblograd__place.-tradition:hover) #tradition,
             &:has(.roblograd__place.-family:hover) #family,
             &:has(.roblograd__place.-hystory:hover) #hystory,
             &:has(.roblograd__place.-development:hover) #development{
                display: block;
                @media (min-width: 768.1px) {
                opacity: 1;
                visibility: visible;
                transform: translateX(0px);
            }
}
.logo {
    width: 110%;
    // width: calc(554 / 643 * 100%);
    @media (max-width: 768px) {
            width: 100%;
    }
    
    img {
    width: 100%;
    }
}

.roblograd {
    padding-top: 40px;
    @media (max-width: 768px) {
            padding: 0;
    }
    grid-row: span 3;
    align-self: center;
		&__wrapper {
            position: relative;
		}

		&__image {
            position: relative;
            @media (max-width: 768px) {
                width: 105%;
                margin: 0 0 0 -20px;
             }

            
		}

             &__map{
                width: 100%;
                height: auto !important;
            }

            &__balloons{
                position: absolute;
                top: 0;
                left: 0;
                width: 60%;
            }

		&__place {
            position: absolute;
            width: 14%;
            height: 13%;
            &.-tradition {
                width: 14%;
                height: 13%;
                top: 22%;
                left: 23%;
                @media (max-width: 768px) {
                left: calc(24.5% - 20px);
                }
            }
            &.-family {
                width: 9.5%;
                height: 12%;
                top: 44%;
                left: 45.5%;
                @media (max-width: 768px) {
                left: calc(48% - 20px);
                }
            }
            &.-hystory {
                width: 14%;
                height: 12.5%;
                top: 33%;
                left: 32%;
                z-index: 2;
                @media (max-width: 768px) {
                left: calc(34% - 20px);
                }
            }
            &.-development {
                width: 13%;
                height: 12.5%;
                top: 51%;
                left: 32%;
                z-index: 2;
                @media (max-width: 768px) {
                left: calc(34% - 20px);
                } 
            }

		}
}

.menu {
        width: 110%;
        padding: 80px 0 0 calc(5% + 3px);
        @media (max-width: 992px) {
            padding: 40px 0 0 calc(5% + 3px);
        }
        @media (max-width: 768px) {
            padding: 43px 0 0 0;
            width: 100%;
        }

		&__nav {
		}
}


.menu__list{
    display: flex;
    flex-direction: column;
    padding: 0;
    li:not(:last-of-type) {
        margin-bottom: 45px;
        
        @media (max-width: 768px) {
        margin-bottom: 0px;        
        }
    }

    @media (max-width: 768px) {
        margin-bottom: 20px;
        flex-direction: row;
        justify-content: center;
        gap: 50px;
        padding: 0 40px;
        
    }
    @media (max-width: 520px) {
        justify-content: space-around;
        gap: 0;
    }

    & .title__link{
      //padding-bottom: calc(20 / 16 * 1rem);
    }
}


.events {
        padding: 20px 0 0 calc(5% - 3px);
        @media (max-width: 768px) {
                padding: 0px;
            }
		&__wrapper {

            position: relative;
            @media (max-width: 768px) {
                padding: 0 21px;
            }
		}

		&__event {
            
            padding: 18px 10px 20px 15px;
            border-radius: 20px;
            display: none;
            @media (min-width: 768.1px) {
                display: block;
                position: absolute;
                // top: -250%;
                top: -250px;
                left: 75%;
                opacity: 0;
                visibility: hidden;
                transform: translateX(-30px);
                transition: all 0.3s;
                width: 75%;
                @media (min-width: 992.1px) {
                    width: 75%;
                top: -300px;
                left: 75%;
            }
                @media (min-width: 1170.1px) {
                    width: 83%;
                top: -330px;
                left: 83%;
            }
            }
            &.-active{
                display: block;
            }
		}

		&__list {
            padding: 0;
		}

		&__point {
            padding: 0 0 0 20px;
            position: relative;
            font-size: calc(10px + 10 * ((100vw - 320px) / ($mw - 320)));
            line-height: 130%;
            &::before {
                content: '';
                display: block;
                width: 8px;
                height: 8px;
                border-radius: 50%;
                position: absolute;
                top: 5px;
                left: 0;
            }

            &_blue{
                &::before{
                    background-color: $blue;
                }
            }

            &_orange{
                &::before{
                    background-color: $orange;
                }
            }

            &_green{
                &::before{
                    background-color: $green;
                }
            }
            &_blue_2{
                &::before{
                    background-color: $blue_2;
                    }
                }
            
		    }
        }
    }
}
